.is-hidden {
  pointer-events: none;
  visibility: hidden;
  opacity: 0;
  transition: visibility .55s cubic-bezier(.4, 0, .2, 1), opacity .55s cubic-bezier(.4, 0, .2, 1);
}

.backdrop {
  z-index: 9999;
  background: var(--violet-gradient);
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.modal {
  border: 2px solid var(--black);
  background-color: var(--project-background);
  border-radius: 18px;
  width: 335px;
  height: 384px;
  transition: transform .5s cubic-bezier(.4, 0, .2, 1);
  position: relative;
  transform: translateY(0);
  box-shadow: 0 1px 3px #0000001f, 0 1px 1px #00000024, 0 2px 1px #0003;
}

.modal__close {
  background-color: var(--white);
  cursor: pointer;
  stroke: var(--black);
  background-color: #0000;
  border: none;
  border-radius: 50px;
  outline: none;
  padding: 0;
  transition: transform .5s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 15px;
  right: 19px;
}

.modal__close:hover {
  stroke: var(--violet);
}

.modal__wrapper {
  padding: 40px 20px;
}

.modal__wrapper--input {
  font-family: var(--font-primary);
  flex-direction: column;
  margin-bottom: 8px;
  display: flex;
}

.modal__container {
  flex-direction: column;
  justify-content: center;
  display: flex;
}

.modal__container--text {
  display: inline-grid;
}

.modal__box {
  margin-bottom: 24px;
  display: flex;
  position: relative;
}

.modal__box--input {
  color: var(--black);
  background-color: var(--project-background);
  border: 2px solid var(--black);
  border-radius: 40px;
  outline: none;
  width: 100%;
  padding-top: 16px;
  padding-bottom: 16px;
  padding-left: 24px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.25;
  transition-property: border;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.modal__send {
  background: var(--black);
  color: var(--project-background);
  cursor: pointer;
  letter-spacing: .06em;
  text-align: center;
  border: none;
  border-radius: 40px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 50px;
  margin-top: 24px;
  padding: 16px 0;
  font-size: 16px;
  font-weight: 600;
  line-height: 1.21;
  display: flex;
}

.modal__navigation {
  justify-content: center;
  gap: 20px;
  margin-top: 14px;
  font-size: 14px;
  display: flex;
}

#show-sign-up-modal-top:hover, #show-sign-in-modal-top:hover, #show-sign-up-modal-bottom:hover, #show-sign-in-modal-bottom:hover, #show-sign-up-modal-top.active, #show-sign-in-modal-bottom.active {
  color: var(--violet);
  text-decoration: underline;
}

.is-hidden .modal {
  transform: translateY(-150%);
}

.modal__box--input:not(:placeholder-shown):required:valid {
  border-color: #3cbc81;
}

.modal__box--input:not(:placeholder-shown):required:invalid {
  border-color: #e74a3b;
}

@media screen and (min-width: 769px) {
  .modal {
    width: 579px;
    height: 516px;
  }

  .modal .modal__close {
    top: 24px;
    right: 24px;
  }

  .modal .modal__close--dimension {
    width: 28px;
    height: 28px;
  }

  .modal .modal__wrapper {
    padding: 80px 40px;
  }

  .modal .modal__box {
    margin-bottom: 28px;
  }

  .modal .modal__box--input {
    padding-top: 20px;
    padding-bottom: 20px;
    padding-left: 40px;
    font-size: 18px;
  }

  .modal .modal__send {
    height: 64px;
    margin-top: 12px;
    padding: 20px 0;
    font-size: 18px;
  }

  .modal .modal__navigation {
    margin-top: 20px;
    font-size: 18px;
  }
}

